import React, { Component } from "react";
import MKTypography from "components/MKTypography";

import "./css/index.css";

function sleep(ms) {
  return new Promise((resolve) => setTimeout(resolve, ms));
}

class TypeAsyncComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      text: "",
      isAnimating: true, //  
    };
    this.typeInterval = 100;
  }

  componentDidMount() {
    this.init();
  }

  async init() {
    while (this.state.isAnimating) {
      await this.type("# ");
      await sleep(2000);
      await this.delete("# ");
      await this.type("# ");
      await sleep(2000);
      await this.delete("# ");
      await this.type("#  ");
      await sleep(2000);
      await this.delete("#  ");
    }
  }

  async type(text) {
    for (let character of text) {
      await sleep(this.typeInterval);
      this.setState((prevState) => ({
        text: prevState.text + character,
      }));
    }
  }

  async delete(text) {
    for (let i = 0; i < text.length; i++) {
      await sleep(this.typeInterval);
      this.setState((prevState) => ({
        text: prevState.text.slice(0, -1),
      }));
    }
  }

  render() {
    const { text } = this.state;

    return (
      <MKTypography
        className="type-async"
        color="white"
        opacity={0.9}
        sx={{
          fontSize: "1.5rem", //   
        }}
      >
        <span>{text}</span>
        <span className="blinking-cursor">_</span>
      </MKTypography>
    );
  }
}

export default TypeAsyncComponent;
